"A continuación, encuentra algunos conceptos fundamentales con respecto al almacenamiento dentro de la cadena logística.")]),_vm._m(25),_c('p',{staticClass:"mb-5"},[_vm._v("El almacén hace parte fundamental de la cadena de logística y su función general es la de regular el flujo de las mercancías dentro de la red logística. Gestionar adecuadamente el almacén no solo contribuye al flujo de las mercancías, sino también ayuda a la estabilidad de los procesos internos de la empresa. Asimismo, es importante para sincronizar la oferta y la demanda en el sentido en que ayuda a que a determinadas cantidades demandadas de la oferta del producto o mercancía sean entregadas satisfactoriamente, sin tiempos prolongados de espera, o sea, garantiza la oferta constante de los productos o materias primas. Todo esto requiere de un control adecuado de las existencias en el almacén. (Riveros, 2015).")]),_vm._m(26),_c('br'),_c('br'),_vm._m(27),_c('p',{staticClass:"mb-5"},[_vm._v("Urzelai (2015) describe a continuación la clasificación de diferentes tipos de almacenes, que se pueden agrupar teniendo en cuenta dos criterios:")]),_c('div',{staticClass:"row mb-5"},[_c('div',{staticClass:"col-md-6 mb-4 mb-md-0"},[_vm._m(28),_vm._m(29),_vm._m(30),_c('ul',{staticClass:"lista-ul--color"},[_c('li',[_c('i',{staticClass:"fas fa-check"}),_c('p',[_c('b',[_vm._v("Almacén de fábrica. ")]),_c('b-none',[_vm._v("Su ubicación se encuentra en las propias instalaciones de la fábrica, desde donde recibe los productos y los almacena hasta su despacho.")])],1)]),_c('li',[_c('i',{staticClass:"fas fa-check"}),_c('p',[_c('b',[_vm._v("Almacén regulador. ")]),_c('b-none')],1)]),_c('li',[_c('i',{staticClass:"fas fa-check"}),_c('p',[_c('b',[_vm._v("Almacén de distribución. ")]),_c('b-none',[_vm._v("Da servicio a una zona geográfica concreta. Regular el flujo de los productos a lo largo de los canales de distribución, recibiendo los productos de fábrica y distribuyéndolos a los puntos de destino según las necesidades.