e pedido y envío, el cual funciona de la siguiente manera: se envía el pedido, vía sistema electrónico de datos (EDI), al proveedor, quien consolida la información y entonces empieza a hacer la selección de los productos para después enviarlos al centro de distribución. Este a su vez realiza la consolidación y verificación de los documentos, luego pasa a un proceso de recepción en el cual se deja registrada la mercancía en el centro de distribución. Sigue un proceso de consolidación de mercancía por parte del almacén a lo que es llamado destelle y, una vez finalizado esto, se procede a la conformación del proceso completado, luego se procede al embarque de la mercancía. Finalmente, una vez transportada, la mercancía queda a disposición del cliente de acuerdo con las condiciones acordadas. (Monzó, 2010).")])],1)])]),_c('div',{staticClass:"row mb-3"},[_vm._m(37),_c('div',{staticClass:"col-lg-10"},[_c('p',[_c('b',[_vm._v("Bodegas. ")]),_c('b-none',[_vm._v("Según las características físicas de las bodegas, estas se pueden clasificar en bodega general, bodega de rotación y centros de acopio. (Riveros, 2015).")])],1)])]),_c('div',{staticClass:"row mb-3"},[_vm._m(38),_c('div',{staticClass:"col-lg-10"},[_c('p',[_c('b',[_vm._v("Back office o trastienda. ")]),_c('b-none',[_vm._v("Es la parte de las organizaciones en donde se realizan las labores destinadas a la gestión de la propia de la empresa con las cuales el cliente no tiene un contacto directo: por ejemplo, el departamento de informática o el de recursos humanos, entre otros.")])],1)])]),_c('div',{staticClass:"row mb-3"},[_vm._m(39),_c('div',{staticClass:"col-lg-10"},[_c('p',{staticClass:"mb-5"},[_c('b',[_vm._v("Zona franca. ")]),_c('b-none',[_vm._v("El Ministerio de Industria y Comercio de Colombia (2012) define las zonas francas como “áreas geográficas delimitadas del territorio nacional, en donde se desarrollan actividades industriales de bienes y de servicios o actividades comerciales, bajo una normatividad especial en materia tributaria, aduanera y de comercio exterior”.")]),_c('br'),_c('br'),_vm._v("Una zona franca es entonces un territorio delimitado por un país en donde la principal idea es gozar de ciertos beneficios tributarios, como por ejemplo la excepción del pago de derechos de importación de las mercancías o de algunos impuestos o la regulación de estos. (Riveros, 2015).")],1)])]),_c('div',{staticClass:"row mb-3"},[_vm._m(40),_c('div',{staticClass:"col-lg-10"},[_c('p',{staticClass:"mb-5"},[_c('b',[_vm._v("Punto de venta (PDV). ")]),_c('b-none',[_vm._v("Es el lugar en el que se efectúan las transacciones comerciales de compraventa. Cuenta con una caja registradora, un recaudador o cajero y una computadora con un programa para recaudar el dinero, entre otros. Se puede denominar al punto de venta como el conjunto de accesorios y programas que, funcionando conjuntamente, efectúan operaciones que permiten la impresión de un ticket o factura de venta, además de emitir reportes relacionados con ellas y también llevar el control de inventarios y de operaciones comerciales establecidas. Actualmente existen también cajas registradoras fiscales, es decir, cajas que tienen una autorización especial por parte de la autoridad gubernamental para que funcionen como un sistema de comprobación fiscal y que emiten tickets fiscales que pueden ser utilizados como comprobantes de pago para la declaración de impuestos. Hoy en día en los negocios y tiendas se utilizan los puntos de venta de manera variada, es decir, que se han implementado tanto en las pequeñas tiendas de abarrotes como en las grandes cadenas de supermercados y hasta en las tiendas de venta de cualquier tipo de artículos. (Riveros, 2015).")])],1)])]),_c('div',{staticClass:"row mb-3"},[_vm._m(41),_c('div',{staticClass:"col-lg-10"},[_c('p',{staticClass:"mb-5"},[_c('b',[_vm._v("Transporte dentro de la cadena logística. ")]),_c('b-none',[_vm._v(" Hace referencia a la forma como se van a transportar los productos desde la planta productora al distribuidor o, dado el caso, al consumidor final. Aquí se debe cuidar las condiciones en que viajan los productos para evitar su deterioro y posibles malestares a los clientes. De igual manera, a la entrega a tiempo se le debe hacer un estricto control, ya que en el momento es una de las grandes exigencias del mercado convirtiéndose en uno de los elementos fundamentales de un programa de servicio al cliente. Se debe, además, hacer una correcta manipulación de los productos y el arrume según lo exige el cliente. Para el transporte de las mercancías se pueden tener varias opciones: tener su propia flotilla de camiones o por contratos con empresas que prestan este servicio. Para cualquiera de las dos opciones las compañías deben relacionar los aspectos costos, cuidado de mercancías y trato con los clientes. (Prettel, 2016).")]),_c('br'),_c('br'),_vm._v("La elección del transporte tiene repercusiones en los precios de los productos, el desempeño de la entrega y las condiciones de los bienes a su llegada y todo ello incide sobre la satisfacción del cliente. Al enviar mercancías a sus bodegas, distribuidores y clientes, una compañía elige entre cinco modos principales de transporte: (Kotler, 2017)."),_c('br'),_c('br'),_vm._m(42)],1)])]),_c('div',{staticClass:"row mb-5"},[_vm._m(43),_c('div',{staticClass:"col-lg-10"},[_c('p',[_c('b',[_vm._v("Servicios logísticos en el marketing digital. ")]),_c('b-none',[_vm._v("Una empresa de logística, además de ofrecer una serie de servicios, permite la preparación y envío de las mercancías de una forma acorde con la tienda on-line. Es fundamental encontrar una empresa logística que ofrezca el servicio adecuado ya que va a ser esa empresa la que entregará el producto y la única que tendrá contacto físico con el cliente. (Irureta, 2015).")])],1)])]),_c('p',{staticClass:"mb-5"},[_vm._v("La empresa de servicios logísticos para marketing digital debe ofrecer los siguientes servicios:")]),_vm._m(44),_c('br'),_c('br'),_c('h4',[_vm._v("Logística inversa")]),_c('p',{staticClass:"mb-5"},[_vm._v("El flujo de bienes en la cadena de suministro no termina con el consumidor o usuario industrial. En la actualidad, las empresas reconocen que las cadenas de suministro pueden funcionar en el sentido opuesto. La logística inversa es un proceso mediante el cual se recogen materiales reciclables y reutilizables, devoluciones y productos que deben reprocesarse en los puntos de consumo o uso para su reparación, remanufactura, redistribución o desecho. El efecto de la logística inversa puede apreciarse en la reducción de los desperdicios en rellenos sanitarios y de los costos operativos de las empresas. (Kerin, 2018).")]),_vm._m(45),_c('p',{staticClass:"mb-5"},[_vm._v("Adicional a esto para Kotler (2017) la distribución inversa implica reutilizar, reciclar, renovar o desechar productos dañados, no deseados o excedentes que hayan devuelto los consumidores o los distribuidores.")]),_vm._m(46),_c('h4',[_vm._v("Inventario")]),_c('p',{staticClass:"mb-5"},[_vm._v("El inventario es la cantidad de productos con los que cuenta la empresa con anterioridad al proceso de venta y que le permiten llevar a cabo procesos de comercialización. Esto gracias a las existencias del producto en almacenaje, que dan la posibilidad de diseñar estrategias de fabricación, compra y venta de los artículos. Estos inventarios son bienes tangibles que se tienen almacenados para el momento de la venta, pero también se puede tener inventarios de materias primas, repuestos y accesorios, necesarios para el proceso de producción de un producto terminado y para su posterior comercialización. Un adecuado manejo y control de los inventarios es de vital importancia ya que, por medio de actividades y técnicas, mantiene la cantidad de estos en el nivel apropiado, de tal forma que no excedan la capacidad financiera de la empresa en costos ni se corra el riesgo de no contar con las existencias necesarias para la satisfacción de la demanda. Además, tener control y hacer seguimiento a las cantidades de productos almacenados es fundamental para llevar a término la comercialización del producto y brindar un buen servicio al cliente. (Riveros, 2015).")]),_c('div',{staticClass:"h6"},[_vm._v("Gustavo Riveros (2015) clasifica los tipos de inventarios de la siguiente manera:")]),_c('br'),_c('TabsA',{staticClass:"color-secundario mb-5"},[_c('div',{staticClass:"tarjeta color-secundario--borde color-azul p-4",attrs:{"titulo":"Inventario perpetuo"}},[_c('div',{staticClass:"row"},[_c('div',{staticClass:"col-xl-4"},[_c('figure',[_c('img',{staticClass:"m-auto my-4",staticStyle:{"width":"200px"},attrs:{"src":require("@/assets/curso/img158.svg"),"alt":"Texto que describa la imagen"}})])]),_c('div',{staticClass:"col-xl-8"},[_c('p',{staticClass:"pe-5 py-5"},[_vm._v("Es un sistema que tiene un registro constante de las unidades existentes para cada artículo o producto dentro del almacén. En el momento en que las existencias disminuyen, estas son repuestas nuevamente, de tal manera que el inventario siempre está en un nivel determinado por una cantidad de unidades fijas. Esta información actualizada permite tomar decisiones acertadas acerca de precios, términos de comercialización, negociación e inventario con respecto a la demanda.")])])])]),_c('div',{staticClass:"tarjeta color-secundario--borde color-azul p-4",attrs:{"titulo":"Inventario intermitente"}},[_c('div',{staticClass:"row"},[_c('div',{staticClass:"col-xl-4"},[_c('figure',[_c('img',{staticClass:"m-auto",staticStyle:{"width":"200px"},attrs:{"src":require("@/assets/curso/img159.svg"),"alt":"Texto que describa la imagen"}})])]),_c('div',{staticClass:"col-xl-8"},[_c('p',{staticClass:"pe-5 py-5"},[_vm._v("Es un inventario que se realiza varias veces al año con un período de tiempo de intervalo. Este inventario no se realiza al terminar el período contable.")])])])]),_c('div',{staticClass:"tarjeta color-secundario--borde color-azul p-4",attrs:{"titulo":"Inventario final"}},[_c(