ceso de fabricación del producto terminado. Esto permite clasificar los inventarios también de la siguiente forma:")]),_vm._m(47),_c('br'),_c('br'),_c('h4',[_vm._v("Stock de inventarios")]),_vm._m(48),_c('p',{staticClass:"mb-5"},[_vm._v("Riveros (2015) describe a continuación las clases de existencia o stock:")]),_c('TabsA',{staticClass:"color-secundario mb-5"},[_c('div',{staticClass:"tarjeta color-secundario--borde color-azul p-4",attrs:{"titulo":"Stock de seguridad"}},[_c('div',{staticClass:"row"},[_c('div',{staticClass:"col-xl-4"},[_c('figure',[_c('img',{staticClass:"m-auto d-flex my-5",staticStyle:{"width":"200px"},attrs:{"src":require("@/assets/curso/img168.svg"),"alt":"Texto que describa la imagen"}})])]),_c('div',{staticClass:"col-xl-8"},[_c('p',{staticClass:"pe-5 py-5"},[_vm._v("Son las cantidades de unidades almacenadas que están por encima de las unidades necesarias o normales dentro del almacén. Estas existencias satisfacen un exceso de demanda o un retraso imprevisto del pedido. "),_c('br'),_c('br'),_vm._v("El stock de seguridad es el volumen de mercancías para hacer frente a las fluctuaciones de la demanda, previsiones inexactas, retrasos en la recepción de los pedidos o incidencias en la producción. En otras palabras, el stock de seguridad es el nivel de inventario adicional que se reserva «por si acaso». (Urzelai, 2013).")])])])]),_c('div',{staticClass:"tarjeta color-secundario--borde color-azul p-4",attrs:{"titulo":"Stock activo o cíclico"}},[_c('div',{staticClass:"row"},[_c('div',{staticClass:"col-xl-4"},[_c('figure',[_c('img',{staticClass:"m-auto my-4",staticStyle:{"width":"200px"},attrs:{"src":require("@/assets/curso/img158.svg"),"alt":"Texto que describa la imagen"}})])]),_c('div',{staticClass:"col-xl-8"},[_c('p',{staticClass:"pe-5 py-5"},[_vm._v("Son las unidades almacenadas para satisfacer la demanda normal del proceso de producción.")])])])]),_c('div',{staticClass:"tarjeta color-secundario--borde color-azul p-4",attrs:{"titulo":"Stock sobrante"}},[_c('div',{staticClass:"row"},[_c('div',{staticClass:"col-xl-4"},[_c('figure',[_c('img',{staticClass:"m-auto my-4",staticStyle:{"width":"200px"},attrs:{"src":require("@/assets/curso/img169.svg"),"alt":"Texto que describa la imagen"}})])]),_c('div',{staticClass:"col-xl-8"},[_c('p',{staticClass:"pe-5 py-5"},[_vm._v("Son las unidades de producto o materias primas que dejan de ser necesarias o demandadas, ya sea por la existencia de nuevos productos en el mercado que llegan a sustituir los ya existentes o por la obsolescencia. El stock sobrante debe estar a la vista dentro del almacén para darle una salida, devolverlo al proveedor o venderlo dada la oportunidad.")])])])]),_c('div',{staticClass:"tarjeta color-secundario--borde color-azul p-4",attrs:{"titulo":"Stock especulativo"}},[_c('div',{staticClass:"row"},[_c('div',{staticClass:"col-xl-4"},[_c('figure',[_c('img',{staticClass:"m-auto my-4",staticStyle:{"width":"200px"},attrs:{"src":require("@/assets/curso/img159.svg"),"alt":"Texto que describa la imagen"}})])]),_c('div',{staticClass:"col-xl-8"},[_c('p',{staticClass:"pe-5 py-5"},[_vm._v("O de anticipación: este tipo de stock satisface la necesidad de la demanda de los productos o materias primas que tiene un proceso de producción por estaciones o períodos. Por este motivo el almacén es aprovisionado con los productos o materias primas necesarias hasta su siguiente período de producción, momento en el cual esas unidades de productos se encuentran disponibles. Por otra parte, este aprovisionamiento es especulativo porque se hace cuando los precios del producto se encuentran a la baja para así poder especular, vender a precios más elevados y tener más utilidades o contar con reservas para los momentos en los cuales los productos estén más costosos.")])])])]),_c('div',{staticClass:"tarjeta color-secundario--borde color-azul p-4",attrs:{"titulo":"Stock de presentación"}},[_c('div',{staticClass:"row"},[_c('div',{staticClass:"col-xl-4"},[_c('figure',[_c('img',{staticClas