)])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"spam-text p-3 mb-5"},[_c('p',{staticClass:"mb-0"},[_vm._v(" Estrategia de presión Push")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row mb-5"},[_c('div',{staticClass:"col-md-6 mb-4 mb-md-0"},[_c('p',[_vm._v("En una estrategia de comunicación de presión, la mayor parte del esfuerzo de marketing se concentra en las iniciativas dirigidas a los mayoristas y los minoristas para inducirlos a cooperar con la empresa, a referenciar la marca, a mantener un nivel mínimo de existencias, a mostrar los productos y darles suficiente visibilidad en sus anaqueles. El objetivo es obtener la cooperación voluntaria ofreciendo términos comerciales atractivos, es decir, grandes márgenes, descuentos por cantidad, publicidad local o en tiendas, subvenciones promocionales, distribución de muestras gratuitas en las tiendas, etc. Aquí, la venta y la comunicación personal son los instrumentos de marketing claves. El papel de los representantes de ventas y de los promotores será particularmente importante. Resulta imprescindible un programa de iniciativas para obtener el apoyo de los intermediarios. Cuanto mayor es su poder de negociación, más difícil será para la empresa obtener su apoyo. (Lambin, 2009).")])]),_c('div',{staticClass:"col-lg-6"},[_c('figure',{staticClass:"mb-4"},[_c('img',{attrs:{"src":require("@/assets/curso/img178.jpg"),"alt":"Texto que describa la imagen"}})])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"spam-text p-3 mb-5"},[_c('p',{staticClass:"mb-0"},[_vm._v(" Estrategias de aspiración (pull)")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row mb-5"},[_c('div',{staticClass:"col-md-6 mb-4 mb-md-0"},[_c('p',[_vm._v("Cuando se adopta una estrategia de aspiración, el fabricante enfoca sus esfuerzos de comunicación en el usuario final, evadiendo a los intermediarios e intentando construir una demanda directamente entre los clientes potenciales del segmento objetivo. El objetivo de comunicación es crear una demanda de cliente y una lealtad de marca fuerte entre los consumidores, para poder arrastrar la marca a través del canal de distribución, forzando a los intermediarios a comercializarla para satisfacer la demanda de los consumidores. Para alcanzar estos objetivos, el fabricante deberá invertir la mayor proporción de su presupuesto de comunicación en publicidad en medios masivos, promociones, y esfuerzos de marketing que apunten a ganar las preferencias de los consumidores finales. (Lambin, 2009).")])]),_c('div',{staticClass:"col-lg-6"},[_c('figure',{staticClass:"mb-4"},[_c('img',{attrs:{"src":require("@/assets/curso/img179.jpg"),"alt":"Texto que describa la imagen"}})])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"spam-text p-3 mb-5"},[_c('p',{staticClass:"mb-0"},[_vm._v(" Trade Marketing")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row mb-5"},[_c('div',{staticClass:"col-md-6 mb-4 mb-md-0"},[_c('p',[_vm._v("Es una estrategia del marketing relacional entre el canal de distribución y el productor o proveedor. Se define como un asesor logístico para el canal de distribución en tiendas de pequeña y de gran superficie. Recordemos que el proceso logístico comprende los elementos de la distribución física (almacenamiento, transporte, control de inventario, procesamiento de pedido, manejo de materiales y demás). Comprende el conjunto de acciones que el fabricante desarrolla, de acuerdo con su estrategia de producción, teniendo en cuenta las necesidades de cada uno de sus clientes y de sus formas de venta, con el fin de conseguir un objetivo común: la satisfacción del consumidor. Se trata de crear un conjunto de marketing entre fabricante y distribuidor y de diseñar conjuntamente un plan operativo eficaz con rentabilidad para ambas partes. (Riveros, 2015).")])]),_c('div',{staticClass:"col-lg-6"},[_c('figure',{staticClass:"mb-4"},[_c('img',{attrs:{"src":require("@/assets/curso/img180.jpg"),"alt":"Texto que describa la imagen"}})])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"spam-text p-3 mb-5"},[_c('p',{staticClass:"mb-0"},[_vm._v(" Category Management")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row mb-5"},[_c('div',{staticClass:"col-md-6 mb-4 mb-md-0"},[_c('p',[_vm._v("Con base en esto se puede decir que el category management es una nueva forma de administrar el negocio detallista, gestionando cada categoría como si fuera un negocio independiente y dirigiéndola a las necesidades de los consumidores de cada uno de los puntos de ventas, de manera que se cree una cadena de valor para el consumidor final del producto. (Riveros, 2015)."),_c('br'),_c('br'),_vm._v("Hace uso de la información y de las nuevas tecnologías para escuchar y conocer las tendencias de consumo de sus clientes. Por ejemplo, a través de los lectores de código de barras se pueden crear con el tiempo bases de datos de clientes que den cuenta del perfil de los consumidores que visitan determinado punto de venta y registren hábitos de compra por consumidor en términos de horas o fechas pico de transacciones, frecuencia y cantidad. De este modo es posible abastecer las tiendas con los productos requeridos por sus consumidores en las cantidades demandadas y en el momento preciso de la compra. Así se dirige cada categoría de producto según las necesidades de los consumidores en cada punto de venta. (Riveros, 2015).")])]),_c('div',{staticClass:"col-lg-6"},[_c('figure',{staticClass:"mb-4"},[_c('img',{attrs:{"src":require("@/assets/curso/img181.jpg"),"alt":"Texto que describa la imagen"}})])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"spam-text p-3 mb-5"},[_c('p',{staticClass:"mb-0"},[_vm._v(" Cross Docking")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row mb-5"},[_c('div',{staticClass:"col-md-6 mb-4 mb-md-0"},[_c('p',[_vm._v("Es el equilibrio en un proceso de consolidación de productos y de consolidación de varios pedidos. Su estrategia se fundamenta básicamente en un flujo continuo de productos, ahorro de costos, transporte rápido, a bajo costo, y un soporte para las necesidades de los clientes. La principal evidencia de que se ha implementado un sistema de cross docking es que existe un tiempo de almacenamiento inferior a 24 horas desde el momento en que la mercancía es recibida y se envía a la zona de picking. En este proceso en particular el material se recoge extrayendo unidades o conjuntos embalados de una unidad de embalada superior que contiene más unidades de las mismas y por último se realiza un efectivo intercambio de información. (Riveros, 2015).")])]),_c('div',{staticClass:"col-lg-6"},[_c('figure',{staticClass:"mb-4"},[_c('img',{attrs:{"src":require("@/assets/curso/img182.jpg"),"alt":"Texto que describa la imagen"}})])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row mb-5"},[_c('div',{staticClass:"col-lg-6"},[_c('div',{staticClass:"cajon cajonh2 color-secundario p-4 mb-4"},[_c('p',[_vm._v("Cross docking directo: es el que se produce cuando los paquetes (pallets, cajas, entre otros), previamente seleccionados por el proveedor de acuerdo con sus órdenes locales, son recibidos y transportados a las respectivas puertas de salida para consolidarlos con otros paquetes en el mismo formato, prácticamente sin que haya mayor manipulación. Las entregas son preparadas por el proveedor en función de cada uno de sus clientes finales mediante una unidad de carga específica y, consecutivamente, el centro de distribución identifica, clasifica y envía la mercadería al respectivo cliente. (Papporello, 2013).")])])]),_c('div',{staticClass:"col-lg-6"},[_c('div',{staticClass:"cajon cajonh2 color-secundario p-4 mb-4"},[_c('p',[_vm._v("Cross docking indirecto: en este caso los paquetes son recibidos, fragmentados y reetiquetados dentro de nuevos paquetes en un centro de distribución para entregarlos a los proveedores locales, es decir, que existe mayor manipulación en los paquetes, pero todos manejando el mismo formato. La preparación del pedido por cliente no la hace el distribuidor, sino el proveedor antes de despachar la mercadería. Por lo tanto, es el distribuidor quien consolida la carga para entregar a distintos clientes y agregar otros productos. (Papporello, 2013).")])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row mb-5"},[_c('div',{staticClass:"col-md-6 mb-4 mb-md-0"},[_c('p',[_vm._v("Se debe tener en cuenta el software licenciado que tiene el Sena para Georreferenciación. También se puede utilizar Google Maps, es un servidor de aplicaciones de mapas en la web que pertenece a Alphabet Inc. Ofrece imágenes de mapas desplazables, así como fotografías por satélite del mundo e incluso la ruta entre diferentes ubicaciones o imágenes a pie de calle con Google Street View, condiciones de tráfico en tiempo real (Google Traffic) y un calculador de rutas a pie, en coche, bicicleta (beta) y transporte público y un navegador GPS, Google Maps Go.")])]),_c('div',{staticClass:"col-lg-6"},[_c('figure',{staticClass:"mb-4"},[_c('img',{staticStyle:{"width":"500px"},attrs:{"src":require("@/assets/curso/img183.jpg"),"alt":"Texto que describa la imagen"}})])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"titulo-segundo",attrs:{"id":"t_3_6"}},[_c('h2',[_vm._v("3.6 Plan de distribución")])])},func